html {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: "Golos Text", "JetBrains Mono";
  color: var(--color-text);
  background-color: var(--color-background);
  font-weight: 300;
}

p {
  color: var(--color-text);
  font-size: 32px;
  line-height: 1.2;
}

details summary::-webkit-details-marker {
  display: none;
}

summary {
  list-style: none;
}

pre {
  overflow-x: auto;
  border-radius: 16px;
}

hr {
  margin: 36px 0 36px 0;
}
