.navigate-menu__item {
    border-bottom: 1px solid var(--color-header);
    transition: 50ms;
    transition-property: transform, background-color, border-radius;

    &:last-child {
        border-bottom: none;
    }
    &:hover {
        transform: scale(1.05);
        background-color: var(--color-header);
        border-radius: 9px;
    }
}
