.author {
    display: flex;
    align-items: center;
    padding: 16px;
    background-color: var(--color-surface);
    flex-shrink: 0;
    border-radius: calc(10vw / 5);

    @media screen and (max-width: 949px) {
        border-radius: 0;
        width: 100vw;
        margin-left: -16px;
    }
    @media screen and (max-width: 549px) {
        margin-left: -8px;
    }
}
